import { Component } from '../core/youjun';

export default class TheHeader extends Component {
  constructor() {
    super({
      tagName: 'header',
      state: {
        menus: [
          {
            name: 'Search',
            href: '#/',
          },
          {
            name: 'Movie',
            href: '#/movie?id=tt4520988',
          },
          {
            name: 'About',
            href: '#/about',
          },
        ],
      },
    });
    // í˜ì´ì§€ê°€ ë°”ë€” ë•Œë§ˆë‹¤ ìƒˆë¡œ ëœë”ë§ => class active ì ìš©
    window.addEventListener('popstate', () => {
      this.render();
    });
  }
  render() {
    this.el.innerHTML = /* html */ `
    <a href="#/" class="logo">
      <span>OMDbAPI</span>.COM
    </a>
    <nav>
      <ul>
        ${this.state.menus
          .map((menu) => {
            // ì´ë™í•  í˜ì´ì§€ì˜ ì£¼ì†Œ
            const href = menu.href.split('?')[0];
            // ì‹¤ì œ í˜ì´ì§€ì˜ ì£¼ì†Œ
            const hash = location.hash.split('?')[0];
            const isActive = href === hash;

            return /* html */ `
          <li>
            <a href="${menu.href}" class="${isActive ? 'active' : ''}">
              ${menu.name}
            </a>
          </li>
          `;
          })
          .join('')}
      </ul>
    </nav>
    <a href="#/about" class="user">
      <img src="https://lh3.googleusercontent.com/a/AAcHTtcg-XuKSKFMKu4nzKQdS1o-en3j5GsC_-C9I7kUyA=s576-c-no" alt="User">
    </a>
    `;
  }
}
