import { Component } from '../core/youjun';
import movieStore from '../store/movie';
import MovieItem from './MovieItem';

export default class MovieList extends Component {
  // constructorë¥¼ ë§Œë“¤ì–´ì„œ ì–´ë–¤ ì¡°ê±´ì´ ë°œìƒë˜ë©´ movieStore.subscribeê°€ ì‘ë™ë  ìˆ˜ ìˆë„ë¡(ìƒˆë¡œ ê²€ìƒ‰í•  ë•Œë§ˆë‹¤ ì›¹í˜ì´ì§€ê°€ ê°±ì‹ ë  ìˆ˜ ìˆë„ë¡) í•œë‹¤.
  constructor() {
    super();
    // movieStoreì— ì €ì¥ëœ ê°’ ì¤‘ì—ì„œ movies ë¼ëŠ” ëŒ€ìƒì„ ê°ì‹œí•œë‹¤.
    // ìƒˆë¡œ moviesê°€ ê°±ì‹ ë  ë•Œë§ˆë‹¤ ë‹¤ì‹œ render() ëœë‹¤.

    movieStore.subscribe('movies', () => {
      this.render();
    });
    movieStore.subscribe('loading', () => {
      this.render();
    });
    movieStore.subscribe('message', () => {
      this.render();
    });
  }
  render() {
    this.el.classList.add('movie-list');
    this.el.innerHTML = /* html */ `
        ${
          movieStore.state.message
            ? `<div class="message">${movieStore.state.message}</div>`
            : '<div class="movies"></div>'
        }
        <div class="the-loader hide"></div>
        `;

    const moviesEl = this.el.querySelector('.movies');
    // ì—ëŸ¬ ì œê±°
    moviesEl?.append(
      ...movieStore.state.movies.map(
        (movie) => new MovieItem({ movie }).el // movie = movie : movie
        // MovieItem ì„ ì •ë¦¬í•´ì„œ ë³´ì—¬ì¤„ componentë¥¼ ë§Œë“¤ì–´ì„œ movie ë°ì´í„°ë¥¼ ë„£ì–´ì¤€ë‹¤.
        // MovieItem ì»´í¬ë„ŒíŠ¸ì—ì„œ ë§Œë“¤ì–´ì§„ ê°ê°ì˜ í˜•ì‹ì„ moviesElì— mapì„ í†µí•´ ëª¨ë‘ ë³´ì—¬ì¤€ë‹¤.
      )
    );

    const loaderEl = this.el.querySelector('.the-loader');
    movieStore.state.loading
      ? loaderEl.classList.remove('hide')
      : loaderEl.classList.add('hide');
  }
}

